body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Base styles for all screen sizes */
.header-main {
  background-color: #131313;
  height: 103px;
  border-bottom-left-radius: 45px;
  border-bottom-right-radius: 45px;
  box-shadow: 13px 7px 21px 4px #131313;
  position: absolute;
  z-index: 2;
  width: 100%;
}

.headcontain {
  max-width: 1410px;
  width: 100%;
  margin: 0 auto;
  display: flex;
}

.megaMenu-container {
  width: 94%;
  display: flex;
  flex-wrap: wrap;
  padding: 14px 0px 0px 0px;
  background-color: black;
  border-radius: 100px;
  height: 65px;
  margin-top: 17px;
}

.category-container {
  display: flex;
  border-radius: 20px;
}

.head-left {
  margin-left: 0px;
}

.head-right {
  margin-right: 25px;
}

a {
  color: white;
  font-size: 24px;
}


.content-in:hover {
	border-bottom: 2px solid white;
}

li {
  padding: 0px 25px;
}

.lang-switch {
  font-size: 16px;
}

.lang-lev1 {
  padding-left: 11px;
  padding-right: 0px;
  padding-top: 6px;
}

.logo-container {
  display: flex;
  width: 12%;
  margin-top: 10px;
  flex-wrap: wrap;
}

.logo-container img {
  width: 140px;
  height: 77px;
}

.header-banner {
  position: relative;
  overflow: hidden;
  width: 100%;
}

/* Media query for tablets */
@media (max-width: 768px) {
  .megaMenu-container {
    /* Adjust styles for tablets */
    /* For example, reduce padding or font size */
    width: 85%;
    margin-left: 23px;
  }

  .head-right {
    display: none;
  }

  .logo-container {
    width: 15%;
  }
}

/* Media query for mobile devices */
@media (max-width: 480px) {
  .megaMenu-container {
    width: 75%;
    justify-content: center;
  }
  .logo-container {
    width: 25%;
  }
  .category-container {
    flex-direction: column;
  }
  

  li {
    padding: 10px 0;
    /* Add spacing between menu items */
  }
  
}
.center-logo {
    display: flex;
    align-items: center;
    display: flex;
    align-items: center;
    margin-bottom: 65px;
    padding-top: 32px;

}

.center-logo img {
    width: 205px;
    height: 129px;
}

.contact-info {
    width: 100%;
    margin: 0 auto;
    text-align: left;
    padding-top: 32px;
    display: flex;
    max-width: 1410px;
    flex-wrap: wrap;
}

.info-tabs {    
    width: 33.33%;
    display: flex;
    color: white;

}

.info-tabs.social-temp {
	width: 33.33%;
	display: flex;
	flex-direction: column;
	font-size: 32px;
	color: white;
	flex: 1 1;
	align-self: end;
	margin-bottom: 20px;
}

.manage {
    font-size: 45px;
}

.info-tabs.manage li:nth-child(2),
.info-tabs.manage li:nth-child(3) {
