}

.our-mission {
    font-size: 17px;
    padding-bottom: 25px;
    padding-top: 10px;
}

.mail-container image {
    background-color: aliceblue;
    color: aliceblue;

}



.footer-conatiner {
    position: absolute;
    left: 0;
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    width: 100%;
    overflow: hidden;
    background-color: black;
    position: absolute;
    z-index: 2;
    width: 100%;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;

}

.social-net {
	display: flex;
	grid-gap: 40px;
	gap: 40px;
	margin: 0px 50px 29px 3px;
}
.con-social {
	background-color: #cacaca;
	padding: 2px 12px 3px 12px;
	border-radius: 50%;
}

.sum-mod {  width: 26px;  height: 24px; }

.our-vision li {
    line-height: 36px;
    font-size: 18px;
    color: #cacaca;
}

/*email-input*/
form {
    display: flex;
    /* Use flexbox to arrange items in a row */
    flex-wrap: wrap;
    /* Allow items to wrap to the next line if necessary */
    justify-content: center;
    /* Center items horizontally */
}

/* Style for the input group */
.input-group {
    display: flex;
    /* Use flexbox for the input and button */
    margin-right: 11px;
    /* Add some spacing between input and button */
}

/* Style for the email input */
input[type="email"] {
	width: 100%;
	padding: 15px 25px 15px 13px;
	border: none;
	font-size: 16px;
	color: #cacaca;
	background-color: #131313;
	margin-right: 1px;
}

/* Style for the email input when it's focused */
input[type="email"]:focus {
    border-color: #858585;
    outline: none;
}

/* Style for the placeholder text in the email input */
input[type="email"]::-webkit-input-placeholder {
    color: #999;
}
input[type="email"]::placeholder {
    color: #999;
}

/* Style for the button */
button {
	background-color: #131313;
	color: #858585;
	border: none;
	padding: 10px 28px;
	font-size: 16px;
	cursor: pointer;
}

/* Style for the button on hover */
button:hover {
    /* background-color: #858585;*/
}










/*rights*/
