/*media for tab**/
@media (max-width:768px) {
    .info-tabs.social-temp {
        width: 100%;
        margin-top: 45px;
        margin-bottom: 45px;
    }
    .info-tabs.tabular {
        width: 49%;
        margin-top: 45px;
        margin-bottom: 45px;
    }
}

@media (max-width:480px) {
    .center-logo {
        margin-bottom: 5px; 
    }
    .center-logo img {
        height: auto;
        max-width: 178px;
    }
    .contact-info {
        padding-top: 0px;
    }
    .info-tabs.tabular {
        margin-bottom: 5px;
    }
    .manage{
        font-size: 38px;
    }
    .info-tabs.manage li:nth-child(2), .info-tabs.manage li:nth-child(3) {
        font-size: 38px;
    }
    .our-vision li {
        line-height: 25px;
        font-size: 15px;
    }

    .social-net {
        justify-content: space-between;
        align-items: center;
        grid-gap: 30px;
        gap: 30px;
    }
}


/* HomePage.css */
/* Base styles for all screen sizes */
.banner-container {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	overflow: hidden;
	top: 2%;
	left: 0%;
	overflow: hidden;
}

.banner-image {
	width: 100%;
	height: 750px;
	z-index: 1;
  }

.banner-text {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	position: absolute;
	text-align: center;
	border-radius: 20px;
	padding: 12px 55px 19px 55px;
	z-index: 2;
	bottom: 20px;
	top: 44%;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
}

.banner-text p {
	font-size: 17px;
	font-weight: 200;
	color: white;
}

.cta-container {
	background-color: black;
	opacity: 0.8;
	border-radius: 20px;
	padding: 12px 55px 19px 55px;
	margin-bottom: 10px;
	
}

.cta-container h1 {
	font-size: 32px;
	color: white;
}

.ctaInfo-container {
	background-color: #131313;
	opacity: 0.8;
	border-radius: 20px;
	padding: 10px 40px 12px 20px;
	text-align: center;
	box-shadow: 5px 11px 25px 3px #131313;
}

.ctaInfo-container p {
	font-size: 16px;
	font-weight: 200;
}

.ctadownarrow-container {
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	z-index: 2;
	bottom: 45px;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	background-color: #141212bf;
	border-radius: 50%;
	padding: 14px 22px 15px 24px;
}

.fa-down-base {
	color: #ffffff;
	font-size: 52px;
	/* Adjust the icon size as needed */
	-webkit-transform: scaleY(1.0);
	        transform: scaleY(1.0);
	/* Adjust the value as needed to make it thinner */
}


/* Portfolio */

.our-team {
	display: flex;
	flex-wrap: wrap;
	width: 100%;
	background-color: #131313;
	margin: 0 auto;
	max-width: 1410px;
	text-align: center;
	align-items: center;
}

.team-there {
	width: 33.33%;
	margin-top: 90px;
	margin-bottom: 115px;
}

.team-there img {
	max-width: 280px;
	width: 100%;
	height: 391px;
	border-radius: 25px;
}

.team-section-main {
	background-color: #131313;
	width: 100%;
	z-index: 0;
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	overflow: hidden;
	top: -1%;
	left: 0%;
	overflow: hidden;
	margin-bottom: -29px;
}

/*effects for card */
.team-there:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}
.team-there {
	/* transform: scale(1.2); */
	transition: -webkit-transform 0.5s;
	transition: transform 0.5s;
	transition: transform 0.5s, -webkit-transform 0.5s;
}

/* Media query for tablets */
@media (max-width: 768px) {
	.banner-text {
		position: absolute;
		text-align: center;
		border-radius: 20px;
		z-index: 2;
		bottom: 20px;
		top: 45%;
		left: 1%;
		-webkit-transform: translateX(0%);
		        transform: translateX(0%);
		display: flex;
		flex-direction: column;
		flex-wrap: wrap;
	}

	.cta-container h1 {
		font-size: 23px;
		color: white;
	}

	.team-there {
		width: 100%;
		margin-top: 80px;
		margin-bottom: 80px;
	}
	.team-there img {
		max-width: 475px;
		width: 100%;
		height: 490px;
	}
}


@media (max-width:480px) {
	

	.cta-container h1 {
		font-size: 16px;
		color: white;
	}
	.cta-container {
		padding: 8px 41px 10px 41px;
	}

	.ctaInfo-container p {
		font-size: 13px;
	}
	.ctaInfo-container {
		padding: 10px 37px 12px 28px;
	}

	.ctadownarrow-container {
		width: 55px !important;
